*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

address {
  font-style: normal;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
  --bgColor: linear-gradient(83.2deg, #965de9 10.8%, #6358ee 94.3%);
  --buttons-color: #fff;
  --border-color: #fff;
  --font-color: #fff;
  --placeholder-color: #fff;
  --selection-color: #fff;
  --selection-bg-color: #00000040;
  --rows-size: 320px;
  --icons-hover: #3f57b3;
  --icons-font-size: 12px;
  --icons-font-color: #212121;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

body {
  min-width: 320px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
  font-family: Lexend Deca, sans-serif;
  line-height: 2;
}

@media screen and (max-width: 980px) {
  #NotiflixNotifyWrap {
    margin: auto !important;
    inset: 82px 10px auto !important;
  }
}

.container {
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
}

.search__container {
  background-image: var(--bgColor);
  justify-content: center;
  padding: 10px 20px;
  display: flex;
  box-shadow: 0 1px 3px #0003, 0 1px 1px #00000024, 0 2px 1px -1px #0000001f;
}

.search__box {
  width: 100%;
  max-width: 360px;
  height: 60px;
  border-radius: 120px;
  margin: 0 auto;
  position: relative;
}

.search__find-icon {
  pointer-events: none;
  will-change: transform;
  transform-origin: center;
  -o-transition: transform 400ms 220ms cubic-bezier(.19, 1, .22, 1);
  font-size: 1.22em;
  transition: transform .4s cubic-bezier(.19, 1, .22, 1) .22s;
  left: 5px;
}

.search__find-icon--rotate {
  transform: rotate(90deg);
}

.search__find-icon, .search__go-btn {
  height: 60px;
  width: 68px;
  text-align: center;
  fill: var(--buttons-color);
  position: absolute;
  top: 0;
}

.search__svg {
  height: 100%;
  width: 24px;
  margin: auto;
  display: block;
}

.search__go-btn {
  pointer-events: none;
  will-change: opacity;
  cursor: default;
  opacity: 0;
  -o-transition: opacity 190ms ease-out, transform 260ms cubic-bezier(.19, 1, .22, 1);
  background-color: #0000;
  border: none;
  outline: none;
  transition: opacity .19s ease-out, transform .26s cubic-bezier(.19, 1, .22, 1);
  right: 5px;
  transform: rotate(45deg);
}

.search__go-btn--goin {
  opacity: 1;
  pointer-events: all;
  cursor: pointer;
  transform: rotate(0);
  -webkit-transform: rotate(0);
  -moz-transform: rotate(0);
  -o-transform: rotate(0);
  -o-transition: opacity 190ms ease-out, transform 260ms 20ms cubic-bezier(.19, 1, .22, 1);
  transition: opacity .19s ease-out, transform .26s cubic-bezier(.19, 1, .22, 1) 20ms;
}

.search__border {
  width: 100%;
  max-width: 360px;
  height: 60px;
  display: block;
}

.search__border-path {
  fill: none;
  stroke: var(--border-color);
  stroke-width: 5px;
  stroke-miterlimit: 10;
  stroke-dasharray: 740;
  stroke-dashoffset: 0;
  -o-transition: stroke-dashoffset 400ms cubic-bezier(.6, .04, .735, .99);
  transition: stroke-dashoffset .4s cubic-bezier(.6, .04, .735, .99);
}

.search__box--searching .search__border-path {
  stroke-dasharray: 740;
  stroke-dashoffset: 459px;
  -o-transition: stroke-dashoffset 650ms cubic-bezier(.755, .15, .205, 1);
  transition: stroke-dashoffset .65s cubic-bezier(.755, .15, .205, 1);
}

.search__box--searching .search__input {
  box-shadow: none;
}

.search__input {
  width: 100%;
  height: 100%;
  color: var(--font-color);
  letter-spacing: -.015em;
  background: #fff0;
  border: none;
  border-radius: 120px;
  outline: none;
  padding: 0 68px;
  font-family: Montserrat Alternates, sans-serif;
  font-size: 1.2em;
  font-weight: 400;
  position: absolute;
  top: 0;
  left: 0;
}

@media screen and (min-width: 390px) {
  .search__input {
    transition: box-shadow .65s cubic-bezier(.755, .15, .205, 1);
    box-shadow: 0 6px 8px #00000026;
  }
}

.search__input::-webkit-input-placeholder {
  color: var(--placeholder-color);
}

.search__input::-moz-placeholder {
  color: var(--placeholder-color);
}

.search__input:-ms-input-placeholder {
  color: var(--placeholder-color);
}

.search__input:-moz-placeholder {
  color: var(--placeholder-color);
}

.search__input::selection {
  color: var(--selection-color);
  background: var(--selection-bg-color);
}

.gallery {
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: var(--rows-size);
  grid-gap: 12px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

.gallery__link {
  height: 0;
  opacity: 0;
  text-decoration: none;
  display: flex;
}

.gallery__card {
  width: 100%;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  position: relative;
  box-shadow: 0 1px 3px #0003, 0 1px 1px #00000024, 0 2px 1px -1px #0000001f;
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.gallery__image:hover {
  cursor: zoom-in;
  transform: scale(1.03);
}

.gallery__info {
  padding: 10px 20px;
  display: flex;
}

.gallery__item {
  width: 25%;
  -webkit-user-select: none;
  user-select: none;
  flex-direction: column;
  font-size: 13px;
  display: flex;
}

.gallery__item:nth-child(1) {
  width: 22%;
}

.gallery__item:nth-child(3) {
  width: 28%;
}

.gallery__item > a {
  margin-right: auto;
}

.gallery__icon-wrapper {
  font-size: var(--icons-font-size);
  color: var(--icons-font-color);
  cursor: pointer;
  align-items: center;
  margin-right: auto;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), fill .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.gallery__likes-icon > .liked {
  display: none;
}

.gallery__icon-wrapper:hover {
  fill: var(--icons-hover);
  color: var(--icons-hover);
}

.gallery__icon {
  height: 14px;
  width: 14px;
  margin-right: 4px;
}

.gallery--empty-page:after {
  content: "";
  height: calc(100vh - 200px);
  background-image: url("no-image.91540cb0.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 150px;
  display: block;
}

.loadable {
  opacity: 1;
  height: calc(100% - 70px);
  width: 100%;
  transition: height 1.5s .5s, opacity 1.5s .8s;
}

.pulse {
  animation: pulse 1.4s cubic-bezier(.4, 0, .6, 1) infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }

  50% {
    opacity: .3;
  }
}

.custom-scrollbar::-webkit-scrollbar {
  display: none;
}

.custom-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
  width: 100%;
  height: 100%;
  position: relative;
}

.custom-scrollbar > figure {
  height: 100px;
  z-index: 20000;
  background-color: #ffc2e9;
  position: absolute;
  width: 10px !important;
  margin: 0 !important;
  right: 5px !important;
}

.outliner {
  color: #fff;
  cursor: pointer;
  max-width: 10px;
  text-transform: uppercase;
  width: 100%;
  margin: 0 0 2em;
  font-size: 16px;
  font-weight: 400;
  line-height: 45px;
  text-decoration: none;
  position: relative;
}

.outliner:hover {
  text-decoration: none;
}

.outliner {
  outline-offset: 0px;
  text-shadow: none;
  height: 270px;
  border: 0 solid;
  outline: 1px solid #de415f;
  transition: all 1.25s cubic-bezier(.19, 1, .22, 1);
  box-shadow: inset 0 0 20px #de415f;
}

.outliner:hover {
  outline-offset: 15px;
  text-shadow: 1px 1px 2px #427388;
  border: 1px solid;
  outline-color: #fff0;
  box-shadow: inset 0 0 20px #ff7608, 0 0 15px #ff7608;
}

.notransition {
  -o-transition: none !important;
  transition: none !important;
}

.shine-style {
  color: #f7d4d4;
  text-align: center;
  text-transform: uppercase;
  background: linear-gradient(-30deg, #de415f 50%, #c12c48 50%);
  padding-left: 20px;
  font-size: 20px;
  text-decoration: none;
  display: inline-block;
  overflow: hidden;
  transform: translate(0%);
  box-shadow: 0 20px 50px #00000080;
}

.shine-style:before {
  content: "";
  width: 100%;
  height: 100%;
  opacity: 0;
  background-color: #ad8585;
  transition: opacity .2s ease-in-out;
  position: absolute;
  top: 0;
  left: 0;
}

.shine-style:hover:before {
  opacity: .2;
}

.shine-style span {
  position: absolute;
}

.shine-style span:nth-child(1) {
  width: 100%;
  height: 2px;
  background: linear-gradient(to left, #2b080800, #ff7608);
  animation: animateTop 2s linear infinite;
  top: 0;
  left: 0;
}

@keyframes animateTop {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(-100%);
  }
}

.shine-style span:nth-child(2) {
  height: 100%;
  width: 2px;
  background: linear-gradient(to top, #2b080800, #ff7608);
  animation: animateRight 2s linear -1s infinite;
  top: 0;
  right: 0;
}

@keyframes animateRight {
  0% {
    transform: translateY(100%);
  }

  100% {
    transform: translateY(-100%);
  }
}

.shine-style span:nth-child(3) {
  width: 100%;
  height: 2px;
  background: linear-gradient(to right, #2b080800, #ff7608);
  animation: animateBottom 2s linear infinite;
  bottom: 0;
  left: 0;
}

@keyframes animateBottom {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(100%);
  }
}

.shine-style span:nth-child(4) {
  height: 100%;
  width: 2px;
  background: linear-gradient(#2b080800, #ff7608);
  animation: animateLeft 2s linear -1s infinite;
  top: 0;
  left: 0;
}

@keyframes animateLeft {
  0% {
    transform: translateY(-100%);
  }

  100% {
    transform: translateY(100%);
  }
}

:root {
  --yellow: #feb60a;
  --red: #ff0062;
  --blue: #00dbf9;
  --violet: #da00f7;
}

.loading {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.loading > div {
  width: 3vw;
  height: 3vw;
  background-image: linear-gradient(145deg, #ffffff80 0%, #0000 100%);
  border-radius: 100%;
  margin: 2vw;
  animation: bounce 1.5s linear .5s infinite;
}

.loading__yellow {
  background-color: var(--yellow);
}

.loading__red {
  background-color: var(--red);
  animation-delay: .1s;
}

.loading__blue {
  background-color: var(--blue);
  animation-delay: .2s;
}

.loading__violet {
  background-color: var(--violet);
  animation-delay: .3s;
}

@keyframes bounce {
  0%, 50%, 100% {
    filter: blur();
    transform: scale(1);
  }

  25% {
    filter: blur(3px);
    transform: scale(.6);
  }

  75% {
    filter: blur(3px);
    transform: scale(1.4);
  }
}

.download {
  z-index: 10060;
  will-change: box-shadow, transform;
  height: 48px;
  color: #36395a;
  transition: box-shadow .15s ease, transform .15s ease, opacity 1.5s 0;
  cursor: pointer;
  background: #fcfcfd;
  border: 0;
  border-radius: 6px;
  outline: 0;
  padding: 0 32px;
  font-size: 18px;
  line-height: 2.5;
  display: inline-block;
  position: fixed;
  bottom: 0;
  right: 50%;
  transform: translate(50%, -50%);
  box-shadow: 0 2px 4px #2d234266, 0 7px 13px -3px #2d23424d, inset 0 -3px #d6d6e7;
}

.download:hover {
  transform: translate(50%, calc(-50% - 2px));
  box-shadow: 0 4px 8px #2d234266, 0 7px 13px -3px #2d23424d, inset 0 -3px #d6d6e7;
}

.download:active {
  transform: translate(50%, calc(2px - 50%));
  box-shadow: inset 0 3px 7px #d6d6e7;
}

