.element {
  height: 100%;
  width: 50%;
  -webkit-clip-path: polygon(10% 0, 150% 35%, 100% 65%, 0% 80%);
  clip-path: polygon(10% 0, 150% 35%, 100% 65%, 0% 80%);
  background-color: #be9c9c;
  background-image: url("https://source.unsplash.com/500x500/?pink'");
  background-position: center;
  background-repeat: no-repeat;
  justify-content: center;
  margin: auto;
  transition: all .3s cubic-bezier(.68, -.55, .265, 1.55);
  animation: poly 10s ease-in-out infinite;
  display: flex;
}

.element:hover {
  filter: hue-rotate(50deg);
}

@keyframes poly {
  0% {
    -webkit-clip-path: polygon(10% 0, 150% 35%, 100% 65%, 0% 80%);
    clip-path: polygon(10% 0, 150% 35%, 100% 65%, 0% 80%);
  }

  50% {
    -webkit-clip-path: polygon(0 0, 100% 35%, 100% 65%, 10% 100%);
    -webkit-clip-path: polygon(0 0, 100% 35%, 100% 105%, 10% 100%);
    clip-path: polygon(0 0, 100% 35%, 100% 105%, 10% 100%);
  }

  100% {
    -webkit-clip-path: polygon(10% 0, 150% 35%, 100% 65%, 0% 80%);
    clip-path: polygon(10% 0, 150% 35%, 100% 65%, 0% 80%);
  }
}

body.hidden-scroll {
  overflow: hidden;
}

.sl-overlay {
  z-index: 1035;
  background: #fff;
  display: none;
  position: fixed;
  inset: 0;
}

.sl-wrapper {
  z-index: 1040;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.sl-wrapper * {
  box-sizing: border-box;
}

.sl-wrapper button {
  cursor: pointer;
  background: none;
  border: 0;
  padding: 0;
  font-size: 28px;
}

.sl-wrapper button:hover {
  opacity: .7;
}

.sl-wrapper .sl-close {
  z-index: 10060;
  height: 44px;
  width: 44px;
  color: #000;
  margin-top: -14px;
  margin-right: -14px;
  font-family: Arial, Baskerville, monospace;
  font-size: 3rem;
  line-height: 44px;
  display: none;
  position: fixed;
  top: 30px;
  right: 30px;
}

.sl-wrapper .sl-counter {
  z-index: 1060;
  color: #000;
  font-size: 1rem;
  display: none;
  position: fixed;
  top: 30px;
  left: 30px;
}

.sl-wrapper .sl-navigation {
  width: 100%;
  display: none;
}

.sl-wrapper .sl-navigation button {
  height: 44px;
  width: 22px;
